import Header from "../components/layout/Header";
import Footer from "../components/layout/Footer";
import Link from "next/link";
import Image from "next/image";
import { Car, MapPin, Gauge, Calendar, Fuel, Grid, List, Search, Filter } from "lucide-react";
import type { Metadata } from "next";

export const metadata: Metadata = {
    title: "Voitures d'Occasion à Vendre en Algérie | DzairAuto",
    description: "Découvrez les meilleures offres de voitures d'occasion en Algérie.",
};

const cars = [
    { id: 1, brand: "RENAULT", model: "Clio 5", price: "2 450 000 DA", city: "Alger", km: "5 000 km", year: 2024, fuel: "Essence", transmission: "Manuelle", image: "https://images.unsplash.com/photo-1609521263047-f8f205293f24?w=400", isNew: false, date: "Il y a 2 jours" },
    { id: 2, brand: "PEUGEOT", model: "208 GT", price: "3 200 000 DA", city: "Oran", km: "12 000 km", year: 2023, fuel: "Essence", transmission: "Automatique", image: "https://images.unsplash.com/photo-1618843479313-40f8afb4b4d8?w=400", isNew: false, date: "Il y a 1 semaine" },
    { id: 3, brand: "HYUNDAI", model: "i30", price: "3 800 000 DA", city: "Constantine", km: "74 000 km", year: 2022, fuel: "Diesel", transmission: "Manuelle", image: "https://images.unsplash.com/photo-1606611013016-969c19ba27bb?w=400", isNew: false, date: "Il y a 3 jours" },
    { id: 4, brand: "VOLKSWAGEN", model: "Taos", price: "6 000 000 DA", city: "Annaba", km: "24 000 km", year: 2023, fuel: "Essence", transmission: "Automatique", image: "https://images.unsplash.com/photo-1606016159991-dfe4f2746ad5?w=400", isNew: false, date: "Il y a 1 mois" },
    { id: 5, brand: "HYUNDAI", model: "Elantra", price: "5 300 000 DA", city: "Blida", km: "402 km", year: 2024, fuel: "Essence", transmission: "Automatique", image: "https://images.unsplash.com/photo-1621007947382-bb3c3994e3fb?w=400", isNew: true, date: "Il y a 5 jours" },
    { id: 6, brand: "KIA", model: "Sportage", price: "7 500 000 DA", city: "Alger", km: "15 000 km", year: 2023, fuel: "Diesel", transmission: "Automatique", image: "https://images.unsplash.com/photo-1619767886558-efdc259cde1a?w=400", isNew: false, date: "Il y a 2 semaines" },
    { id: 7, brand: "TOYOTA", model: "Corolla", price: "8 200 000 DA", city: "Oran", km: "8 000 km", year: 2024, fuel: "Hybride", transmission: "Automatique", image: "https://images.unsplash.com/photo-1623869675781-80aa31012a5a?w=400", isNew: false, date: "Il y a 4 jours" },
    { id: 8, brand: "DACIA", model: "Sandero", price: "2 900 000 DA", city: "Sétif", km: "32 000 km", year: 2022, fuel: "GPL", transmission: "Manuelle", image: "https://images.unsplash.com/photo-1619405399517-d7fce0f13302?w=400", isNew: false, date: "Il y a 1 semaine" },
];

const brands = ["RENAULT", "PEUGEOT", "HYUNDAI", "KIA", "VOLKSWAGEN", "FIAT", "TOYOTA", "DACIA"];
const cities = ["Alger", "Oran", "Constantine", "Annaba", "Blida", "Sétif", "Batna", "Tlemcen"];

export default function OccasionsPage() {
    return (
        <div className="min-h-screen bg-gray-50">
            <Header />

            {/* Page Header */}
            <section className="bg-[#1a1a2e] text-white py-12">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="flex items-center gap-2 mb-3">
                        <Car className="w-5 h-5 text-[#D32F2F]" />
                        <span className="text-[#D32F2F] font-semibold text-sm uppercase">Annonces vérifiées</span>
                    </div>
                    <h1 className="text-4xl font-bold mb-2">
                        VOITURES D'OCCASION <span className="text-[#D32F2F]">À VENDRE</span>
                    </h1>
                    <p className="text-gray-400">{cars.length} véhicules disponibles à travers l'Algérie</p>
                </div>
            </section>

            {/* Content */}
            <section className="py-8">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="flex flex-col lg:flex-row gap-8">

                        {/* Filters Sidebar */}
                        <aside className="lg:w-72 flex-shrink-0">
                            <div className="bg-white rounded-xl shadow-lg p-5 sticky top-28">
                                <h3 className="font-bold text-gray-900 mb-4 flex items-center gap-2">
                                    <Filter className="w-5 h-5 text-[#D32F2F]" />
                                    Filtres
                                </h3>

                                {/* Search */}
                                <div className="relative mb-4">
                                    <input
                                        type="text"
                                        placeholder="Rechercher..."
                                        className="w-full pl-10 pr-4 py-3 bg-gray-50 border border-gray-200 rounded-lg text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20"
                                    />
                                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                                </div>

                                {/* Brand */}
                                <div className="mb-4">
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Marque</label>
                                    <select className="w-full px-4 py-3 bg-gray-50 border border-gray-200 rounded-lg text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20">
                                        <option>Toutes les marques</option>
                                        {brands.map((brand) => (
                                            <option key={brand}>{brand}</option>
                                        ))}
                                    </select>
                                </div>

                                {/* City */}
                                <div className="mb-4">
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Ville</label>
                                    <select className="w-full px-4 py-3 bg-gray-50 border border-gray-200 rounded-lg text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20">
                                        <option>Toutes les villes</option>
                                        {cities.map((city) => (
                                            <option key={city}>{city}</option>
                                        ))}
                                    </select>
                                </div>

                                {/* Price */}
                                <div className="mb-4">
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Prix</label>
                                    <select className="w-full px-4 py-3 bg-gray-50 border border-gray-200 rounded-lg text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20">
                                        <option>Tous les prix</option>
                                        <option>Moins de 2M DA</option>
                                        <option>2M - 4M DA</option>
                                        <option>4M - 6M DA</option>
                                        <option>6M - 10M DA</option>
                                        <option>Plus de 10M DA</option>
                                    </select>
                                </div>

                                <button className="w-full py-3 bg-[#D32F2F] text-white rounded-lg font-semibold hover:bg-[#B71C1C] transition-colors">
                                    Appliquer les filtres
                                </button>
                            </div>
                        </aside>

                        {/* Cars Grid */}
                        <div className="flex-1">
                            {/* Toolbar */}
                            <div className="flex items-center justify-between mb-6 bg-white rounded-xl p-4 shadow-sm">
                                <p className="text-gray-600">
                                    <span className="font-bold text-gray-900">{cars.length}</span> véhicules trouvés
                                </p>
                                <div className="flex items-center gap-2">
                                    <button className="p-2 rounded-lg bg-[#D32F2F] text-white">
                                        <Grid className="w-5 h-5" />
                                    </button>
                                    <button className="p-2 rounded-lg bg-gray-100 text-gray-600 hover:bg-gray-200">
                                        <List className="w-5 h-5" />
                                    </button>
                                </div>
                            </div>

                            {/* Grid */}
                            <div className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-6">
                                {cars.map((car) => (
                                    <Link
                                        key={car.id}
                                        href={`/occasions/${car.id}`}
                                        className="group bg-white rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all hover:-translate-y-2 border border-gray-100"
                                    >
                                        <div className="relative h-44 overflow-hidden">
                                            <Image src={car.image} alt={`${car.brand} ${car.model}`} fill className="object-cover group-hover:scale-110 transition-transform duration-500" />
                                            {car.isNew && (
                                                <span className="absolute top-3 left-3 px-2 py-1 bg-green-500 text-white text-xs font-bold rounded">NEUVE</span>
                                            )}
                                            <span className="absolute bottom-3 left-3 px-2 py-1 bg-black/60 text-white text-xs rounded">{car.date}</span>
                                        </div>
                                        <div className="p-4">
                                            <h3 className="font-bold text-gray-900 group-hover:text-[#D32F2F] transition-colors">
                                                {car.brand} {car.model}
                                            </h3>
                                            <p className="text-xl font-bold text-[#D32F2F] mt-1">{car.price}</p>
                                            <div className="flex flex-wrap gap-2 mt-3 text-xs text-gray-500">
                                                <span className="flex items-center gap-1"><MapPin className="w-3 h-3" /> {car.city}</span>
                                                <span className="flex items-center gap-1"><Gauge className="w-3 h-3" /> {car.km}</span>
                                                <span className="flex items-center gap-1"><Calendar className="w-3 h-3" /> {car.year}</span>
                                                <span className="flex items-center gap-1"><Fuel className="w-3 h-3" /> {car.fuel}</span>
                                            </div>
                                            <div className="flex gap-2 mt-3 pt-3 border-t border-gray-100">
                                                <span className="px-2 py-1 bg-gray-100 text-gray-600 text-xs rounded">{car.year}</span>
                                                <span className="px-2 py-1 bg-gray-100 text-gray-600 text-xs rounded">{car.transmission}</span>
                                            </div>
                                        </div>
                                    </Link>
                                ))}
                            </div>

                            {/* Pagination */}
                            <div className="flex items-center justify-center gap-2 mt-10">
                                <button className="px-4 py-2 rounded-lg bg-gray-100 text-gray-600 hover:bg-gray-200">Précédent</button>
                                {[1, 2, 3, 4, 5].map((page) => (
                                    <button
                                        key={page}
                                        className={`w-10 h-10 rounded-lg font-medium ${page === 1 ? "bg-[#D32F2F] text-white" : "bg-gray-100 text-gray-600 hover:bg-gray-200"}`}
                                    >
                                        {page}
                                    </button>
                                ))}
                                <button className="px-4 py-2 rounded-lg bg-[#D32F2F] text-white hover:bg-[#B71C1C]">Suivant</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    );
}
